@import "fonts";
@import "mixins";

* {
  margin: 0;
  overflow: hidden;
}

.page {


  &__body {
    margin: 0;
    min-width: 320px;
    height: 100%;
    overflow-y: hidden;
 
}
}
.section{
  display: flex;
  flex-direction: column;
  justify-content: start;
  position: relative;
  height: 100vh;
  width: 100%;
  background: url(../images/bc.png) no-repeat center center; 
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;

  @include onTablet{
    justify-content: center;
  }

  @include onDesktop{
    justify-content: center;
  }
}

.text-block { 
  max-width: 1060px;
  height: 100%;
  padding: 20px 50px 50px 50px;
  margin: 0 auto;
  display: flex;
  position: relative;
  flex-direction:column;
  justify-content: space-between;
  z-index: 1;


  @include onDesktop{
    justify-content: space-between;
    height: 100%;
  }

  @include onDesktopBig{
    justify-content: center;
    height: 100%;
    max-width: 1100px;
  }
  
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  &__subline {
    padding: 6px 15px 6px 8px;
    color: white;
    text-align: center;
    font-family: "Nirmala UI";
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    border: 1px solid #fff;
    backdrop-filter: blur(5px);
    border-radius: 40px;
    margin-bottom: 16px;

    @include onTablet { 
      font-size: 2em;
      line-height: 100%;
      padding: 16px 30px;
      margin-bottom: 20px;
      }
    @include onDesktop { 
      font-size: 2.25em;
      line-height: 100%;
      padding: 16px 30px;
      margin-bottom: 20px;
     }
  }

  &__title{
    font-size: 1.5em;
    text-align: center;
    text-shadow: 0px 4px 20px rgba(255, 213, 54, 0.50);
    font-family: "Nirmala UI Bold";
    font-style: normal;
    font-weight: 700;
    line-height: 120%;
    text-transform: uppercase;
    margin-bottom: 10px;
    background: linear-gradient(178deg, #EB5D38 0%, #FFD600 50%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px #fff;
  
    @include onTablet{
      font-size: 3.25em;
      margin-bottom: 40px;
      -webkit-text-stroke: 2px #fff;
    }
  
    @include onDesktop{
      font-size: 5.25em;
      margin-bottom: 40px;
      -webkit-text-stroke: 2px #fff;
    }
  }

  &__title--bigger {
    font-size: 3em;

    @include onTablet{
      font-size: 5.5em;
    }
  
    @include onDesktop{
      font-size: 7.5em;
    }
  }

  &__description{
    z-index: 1;
    color: #FFF2CE;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-family: "Nirmala UI";
    font-size: 0.75em;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    letter-spacing: 5.6px;
    text-transform: uppercase;
  
    @include onTablet {
      font-size: 1.5em;
      margin-bottom: 80px;
    }

    @include onDesktop {
      font-size: 1.75em;
      margin-bottom: 80px;
    }
  }

  &__description--smaller {
    font-size: 1.1em;
    color: #fff;
    text-transform: initial;

    @include onTablet {
      font-size: 2em;
      margin-bottom: 80px;
    }

    @include onDesktop {
      font-size: 2em;
      margin-bottom: 80px;
    }
  }
  // &__description--last {
  //   text-align: center;
  //   color: #fff;
  //   font-family: "Nirmala UI";
  //   font-size: 1em;
  //   font-style: normal;
  //   line-height: 140%;
  
  //   @include onDesktop{
  //     font-size: 2em;
  //   }
  // }
}

button {
  font-family: "Nirmala UI Bold";
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  color: #FFF;
  border-radius: 10px;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  font-size: 1.5em;
  height: 56px;
  cursor: pointer;

  @include onTablet{
    height: 84px;
  }
  @include onDesktop{
    height: 84px;
  }
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  font-family: "Nirmala UI Bold";
  margin-bottom: 50px;

  @include onTablet {
    gap: 30px;
    width: unset;
    margin-bottom: 70px;
  }

  @include onDesktop {
    display: flex;
    flex-direction: row;
    gap: 50px;
    width: unset;
    margin-bottom: 70px;
  }

  &__cancel {
    width: 100%;
    border: 2px solid #FFF;
    background-color: transparent;
    box-shadow: 4px 4px 20px 0px rgba(255, 255, 255, 0.40);

    @include onTablet {
      width: 50%;

    }
    @include onDesktop {
      width: 50%;
    }
  }

  &__continue {
    width: 100%;
    border: none;
    background: var(--button-gradient, linear-gradient(159deg, #FFE53B 0%, #FF2525 92.71%));
    box-shadow: 4px 4px 20px 0px rgba(255, 255, 255, 0.40);

    @include onTablet {
      width: 50%;

    }
    @include onDesktop {
      width: 50%;
    }
  }
}

.left{
  display: none;
  
  @include onDesktop{
    background-image: url(../images/60e45f206d4da.png);
    display:block;
    position: absolute;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
  }
}

.right{
  background-image: url(../images/small.png);
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: bottom center;

  @include onTablet{
    background-image: url(../images/right-girl.png);
    position: absolute;
  }

  @include onDesktop{
    background-image: url(../images/right-girl.png);
    background-position: bottom right;
  }
}
.girl-right {
  display: none;

  @include onTablet {
    display: none;
  }

  @include onDesktop {
    display: block;
    background-image: url(../images/girl-r.png);

  }
}

.girl-left {
  background-image: url(../images/girl-l.png);
  display:block;
  position: absolute;
  bottom: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;

  @include onDesktop{
    background-image: url(../images/girl-l.png);
    display:block;
    position: absolute;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
  }
}

.visible {
  display: block;
}

.hidden {
  display: none;
}


.carousel {
  align-items: center;
  height: 278px;
  overflow: hidden;
  width: 225px;
  position: relative;
  margin: 0 auto;


  @include onTablet {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;
    width: 100%;
    min-width: 225px;
    position: relative;
    margin-bottom: 50px;
  }

  @include onDesktop {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;
    width: 100%;
    min-width: 225px;
    position: relative;
    margin-bottom: 50px;
  }

  &__item,
  &__item--hidden {
    display: none;
  }
  
  &__item--visible {
    display: flex;
    animation: fade 500ms ease-in-out;
  }
  
  &__action{
    display: flex;
    justify-content: space-between;
    position: relative;
    top: -60%;
    width: 100%;
    transform: translateY(-50%);
  }
}

img{
  object-fit: cover;
  max-width: 100%;
}

@keyframes fade{
   0%{
       transform: translatex(-100%);
  } 
  100%{
      transform: translatex(0);
  }
}

.btn {
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
  position: absolute; 
  width: 51px;
  height: 45px;
  border: none;
  cursor: pointer;

  @include onDesktop{
    width: 134px;
    height: 88px;
  }
  
}

.btn-next{
  background-image: url(../images/right-slide.svg);
  right: 0;
}

.btn-prev{
  background-image: url(../images/left-slide.svg);
  left: 0;
}

.carousel-section__heading {
  font-size: 1.5em;

  @include onDesktop{
    font-size: 4em;
    margin-top: 35px;
  }
}

.carousel-section__wrapper{
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 86em;
  padding: 26px;
  margin: 0 auto;

  @include onDesktop {
    justify-content: space-between;
  }
}
